<template>
  <div>
  <div class="pge">
    <div class="pge_header">
      <x-header :left-options="{showBack:true}">{{$route.meta.title}}</x-header>
    </div>
    <div class="pge_content">
      <group>
        <div class="head_portrait" v-on:click="Picture">
          <cell title="" is-link>
						<span class="pge_pic">
							<img :src="portrait" id="photoImg"/>
						</span>
          </cell>
        </div>
        <cell title="" :value="userInfo.name"  @click.native="showNickName()"></cell>
        <popup-picker title="" :data="sexList" v-model="sexValue"  confirm-text="" @on-change="saveSex" cancel-text="" ></popup-picker>
        <datetime v-model="birthdayVal" title=""  confirm-text="" cancel-text=""  :min-year=1940 :max-year=2017 @on-change="saveBirthday"></datetime>
        <cell title="" is-link link="/member/address"></cell>
      </group>
      <actionsheet v-show="fileOK" v-model="fileOK" :menus="fileMenus" @on-click-menu="changeFile" show-cancel cancel-text=""></actionsheet>
    </div>
  </div>
    <nickname :userInfo="userInfo" ref='nickname' v-on:child-update="updateUserInfo"></nickname>
  </div>
</template>

<script>
  import XHeader from 'vux/src/components/x-header/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'


  import {USERINFO_STORAGE} from '../../common/public-variable'
  import {mapGetters} from 'vuex'
  import Nickname from './nickname.vue'
  var _that;
  export default {
    components: {
      PopupPicker,Actionsheet,XHeader,Cell,Group,XButton,Datetime,Nickname
    },
    data() {
      return {
        userInfo: {},
        fileOK: false,
        isShowNickName: false,
        fileMenus: {
          menu1: '',
          menu2: ''
        },
        sexList: [['','','']],
        sexValue: [''],
        birthdayVal: '2015-11-12'
      }
    },
    computed:{
      ...mapGetters({
        currentDevice:'currentDevice'
      }),
      portrait:function() {
        if (this.userInfo && this.userInfo.portrait) {
          return this.userInfo.portrait;
        }
        else {
          return '../../static/image/touxiang.png';
        }
      }
    },
    methods: {
        updateUserInfo(userInfo){//
            this.userInfo=userInfo;
            this.$setStorage(this.$storage_key.USERINFO_STORAGE,this.userInfo);
        },
      Picture:function () {
        if (this.currentDevice.deviceName == 'wap') {
          this.$vux.toast.show({
            text: this.$tip_message.changePhoto_Err,
            type: 'text',
            width: '22em'
          })
        } else {
          this.fileOK = true;
        }
        this.fileOK = true;
      },
      changeFile:function(key){
        if(key=='cancel')return;

          let sourceType=Camera.PictureSourceType.CAMERA;
        if(key=='menu1'){//
          //
          //navigator.cameraclobbers
          //getPicture
          sourceType=Camera.PictureSourceType.CAMERA;
        }
        else if(key=='menu2') {//
          sourceType=Camera.PictureSourceType.PHOTOLIBRARY;
        }
        navigator.camera.getPicture(takeSuccess, takeFail, {
          quality : 50,
          destinationType : Camera.DestinationType.DATA_URL,
          sourceType : sourceType,
          allowEdit : true,
          encodingType : Camera.EncodingType.JPEG,
          targetWidth : 100,
          targetHeight : 100
        });
      },
      showNickName(){
        this.$refs.nickname.show();
      },
      saveSex(val){
        let sex=val.indexOf('')>-1?1:val.indexOf('')>-1?2:3;
        var that = this;
        this.userInfo.sex=sex;
        that.$post(that.$interface_url.user.modifyInfo, {
          token: this.userInfo.token,
          name: this.userInfo.sex
        }, function (data, success) {
          if (success) {
            that.$setStorage(that.$storage_key.USERINFO_STORAGE,that.userInfo)
            that.$vux.toast.show({
              text: that.$tip_message.update_success,
              type: 'success',
              width: '18em'
            })
          }
          else {
            that.$vux.toast.show({
              text: data,
              type: 'warn',
              width: '18em'
            })
          }
        })
      },
      saveBirthday(val){
          if(val=='2017-01-01'||val==this.userInfo.birthday)return;
        var that = this;
        this.userInfo.birthday=val;
        that.$post(that.$interface_url.user.modifyInfo, {
          token: this.userInfo.token,
          birthday: this.userInfo.birthday
        }, function (data, success) {
          if (success) {
            that.$setStorage(this.$storage_key.USERINFO_STORAGE,that.userInfo)
            that.$vux.toast.show({
              text: that.$tip_message.update_success,
              type: 'success',
              width: '18em'
            })
          }
          else {
            that.$vux.toast.show({
              text: data,
              type: 'warn',
              width: '18em'
            })
          }
        })
      }
    },
    created(){
      this.userInfo=this.$getStorage(this.$storage_key.USERINFO_STORAGE);
      let sex=this.userInfo.sex;
      this.sexValue=sex==1?['']:sex==2?['']:[''];
      this.birthdayVal=this.userInfo.birthday?this.userInfo.birthday:'2017-01-01'
      _that=this;
    }
  }
  //
  function takeSuccess(imageURI) {
    document.getElementById('photoImg').setAttribute('src', 'data:image/jpeg;base64,' + imageURI)
    var user = this.$getStorage(this.$storage_key.USERACCOUNT_STORAGE);
    this.$post(_that.$interface_url.user.modifyHeadImg, {token: _that.userInfo.token, headimg: imageURI}, function (data, success) {
      if (success) {
        _that.$vux.toast.show({
          text: _that.$tip_message.update_success,
          type: 'success',
          width: '15em'
        })
      }
      else {
        _that.$vux.toast.show({
          text: data,
          type: 'text',
          width: '15em'
        })
      }
    })

  }

  //
  function takeFail(message) {
//   alert("" + message);
  }
</script>

<style rel="stylesheet/less" lang="less">
  @import "../../assets/css/Pge.less";
  i {
    font-style: normal;
  }
</style>



