<template>
	<!---->
  <div>

	<div class="retrieve">
		<div class="re_header">
			<x-header :left-options="{showBack:true,backText:''}"></x-header>
		</div>
		<div class="re_content">
			<!---->
			<!-- --''-->
			<!-- --''''-->
			<!--button#99d9fa,#009ff2-->
			<div class="retrieve_password">
				<group>
			      	<x-input title="" v-model="phone"  placeholder="" is-type="china-mobile" :show-clear="true" ref="phoneTxt"></x-input>
			   	</group>
			    <div class="re_btn">
					<x-button type="primary" plain :class="{'weui-btn_plain-primary_active':isClick}" @click.native="nextClick"></x-button>
				</div>
			</div>
		</div>
	</div>
    <sms-code ref="smsCode" :phone="phone" sendType="2" ></sms-code>
  </div>
</template>
<script>
	import XHeader from 'vux/src/components/x-header/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'


  import smsCode from './smscode.vue'
	export default {
    components: {
      XHeader,
      XButton,
      XInput,
      Group,
      smsCode
    },
    data () {
      return {
        isClick: false,
        phone:''
      }
    },
    methods: {
      nextClick(){
        if (!this.isClick)return;
        var that = this;
        that.$post(that.$interface_url.safe.checkPhoneExists, {phone: that.phone}, function (data, success) {
          if (success) {
            that.$refs.smsCode.show();
            that.$refs.smsCode.sendCode();
          }
          else {
            that.$vux.toast.show({text: data, type: 'warn', width: '18em'});
          }
        })
      }
    },
    watch:{
        'phone':{
          handler:function (val,oldval) {
              if(val.trim().length>0&&this.$refs.phoneTxt.valid){
                  this.isClick=true;
              }
              else{
                  this.isClick=false;
              }
            },
          deep:true
        }
    }

  }
</script>

<style rel="stylesheet/less" lang="less">
	@import "../../assets/css/retrieve.less";
	i{
		font-style: normal;
	}
</style>



